import * as classnames from 'classnames';
import * as React from 'react';
import { Layout, Props as LayoutProps } from '../layout';
import { Color, FontSize } from '../text';
import './styles.sass';

export enum Background {
  Base = 1,
  Alt,
  Alt2,
}

const BACKGROUND_CLASSES = {
  [Background.Base]: 'c-background',
  [Background.Alt]: 'c-background-alt',
  [Background.Alt2]: 'c-background-alt-2',
};

const COLOR_CLASSES = {
  [Color.Base]: 'c-text',
  [Color.Alt]: 'c-text-alt',
  [Color.Alt2]: 'c-text-alt-2',
  [Color.Link]: 'c-text-link',
  [Color.Live]: 'c-text-live',
  [Color.Error]: 'c-text-error',
};

const FONT_SIZE_CLASSES = {
  [FontSize.Size1]: 'font-size-1',
  [FontSize.Size2]: 'font-size-2',
  [FontSize.Size3]: 'font-size-3',
  [FontSize.Size4]: 'font-size-4',
  [FontSize.Size5]: 'font-size-5',
  [FontSize.Size6]: 'font-size-6',
  [FontSize.Size7]: 'font-size-7',
  [FontSize.Size8]: 'font-size-8',
};

interface Props extends LayoutProps {
  background?: Background;
  border?: boolean;
  borderTop?: boolean;
  borderRight?: boolean;
  borderBottom?: boolean;
  borderLeft?: boolean;
  children?: React.ReactNode;
  color?: Color;
  fontSize?: FontSize;
}

export const StyledLayout = (props: Props) => {
  const classes: ClassValue = {
    'border-t': props.border || props.borderTop,
    'border-r': props.border || props.borderRight,
    'border-b': props.border || props.borderBottom,
    'border-l': props.border || props.borderLeft,
  };

  if (props.background) {
    classes[BACKGROUND_CLASSES[props.background]] = true;
  }

  if (props.color) {
    classes[COLOR_CLASSES[props.color]] = true;
  }

  if (props.fontSize) {
    classes[FONT_SIZE_CLASSES[props.fontSize]] = true;
  }

  const layoutProps = {
    ...props,
    className: (props.className ? props.className + ' ' : '') + classnames(classes),
  };

  return <Layout {...layoutProps} />;
};



