interface Props {
  asset: SVGAsset;
  height?: number;
  width?: number;
  type?: SVGType;
}

export const SVG = (props: Props) => {
  const classes: ClassValue = {};

  if (props.type) {
    classes[SVG_TYPE_CLASSES[props.type]] = true;
  } else {
    classes[SVG_TYPE_CLASSES[SVGType.Inherit]] = true;
  }

  return (
    <figure className="svg-figure" {...getDataProps(props)}>
      <svg
        className={classnames('svg', `svg--${SVGAsset[props.asset].toLowerCase()}`, classes)}
        width={`${props.width || 16}px`}
        height={`${props.height || 16}px`}
        version="1.1"
        viewBox={`0 0 ${SVG_PATHS[props.asset].width} ${SVG_PATHS[props.asset].height}`}
        x="0px"
        y="0px"
      >
        {SVG_PATHS[props.asset].path}
      </svg>
    </figure>
  );
};



